@import "../variables.scss";

.orderCard {
  padding: 25px;
  border-radius: 20px;
  .orderCartTitle {
    margin-bottom: 30px;
    text-align: start;
    @include text-style(40px, 600, 52px);
  }
  .orderList {
    .cartItem {
      padding: 20px;
      @include flex(flex-start, center);
      margin: 20px;
      width: 600px;
      border: 1px solid $primary1;
      .cartImage {
        width: 40%;
        margin-right: 20px;
      }
      .cartBody {
        text-align: start;
        .productName {
          cursor: pointer;
          margin-bottom: 10px;
          @include text-style(16px, 600, 15px);
          a {
            color: $primary1;
            text-decoration: none;
            &:hover {
              text-decoration: underline;
            }
          }
        }
        .productSize {
          @include text-style(12px, 400, 15px);
        }
        .productQty {
          margin-bottom: 4px;
          @include text-style(12px, 400, 15px);
        }
        .productprice {
          @include text-style(14px, 600, 15px);
        }
      }
      .cartremovebutton {
        margin-left: 30px;
      }
    }
  }
}